.Header {
    text-align: center;
}

.side-menu {
    position: absolute;
    left: 0px;
    top: 50px;
}

a {
    text-decoration: none;
    color:antiquewhite;
}

a:hover {
    color:rgb(194, 189, 182);
}

.bg-collapse {
    border-radius: 0px;
    background-color: rgba(0, 0, 0, 0.548);
}

.fa-solid {
    min-width: 20px;;
}

.modal {
    top: 25%    
}

.modal-content {
    background-image: url('../media/29-290160_purple-and-white-stripes.jpg');
    background-size: 200%;
}

.submit-button {
    background-color: rgba(188, 117, 255, 0.678);
    border: none;
    color: black !important;
}

.submit-button:hover {
    background-color: rgba(208, 167, 250, 0.678);
}

/* flash messages */

.flash-messages {
    position: absolute;
    top: 50px;
    right: 50px;
    padding: 10px;
    background-color:rgba(255, 168, 212, 0.473);
    color:rgb(107, 107, 107);
}

